:root {
    --primary: #ddd;
    --dark: #333;
    --light: #fff;
    --shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
    --blue: #a6d6ec;
}

.site-layout-background {
    background-color: #fff;
    background-color: var(--light);
}

ul {
    justify-content: center;
    list-style: none;
}

.site-content header ul {
    display: grid;
    grid-gap: 5px;
    list-style: none;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    padding: 0px;
    flex-direction: column;
    align-items: start;
    justify-content: center;
}

.site-content li.index-current-value {
    background-color: #ddd;
    background-color: var(--primary);
    padding: 10px;
    border-radius: 15px;
}

.site-content h3 {
    text-transform: uppercase;
    font-size: 1.2em;
}

.site-content li.index-current-value:hover {
    background-color: #a6d6ec;
    background-color: var(--blue);
    color: #fff;
    color: var(--light);
    cursor: pointer;
}

.site-content li.index-current-value.index-current-value h3:hover {
    color: #fff;
    color: var(--light);
    cursor: pointer;
}

.site-content .icon {
    color: green;
}

.charts {
    display: grid;
    grid-gap: 10px;
    /* grid-template-columns: repeat(2, 1fr); */
    grid-template-columns: repeat(2, 1fr);
}

